




























import mediaList from '@/components/mediaList.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
var util = require('../../common/util.js');
var moment = require('moment');
console.log(moment(new Date()).format());
/*
 * 用于记录日期，显示的时候，根据dateObj中的日期的年月显示
 */
var dateObj = (function() {
	var _date = new Date(); // 默认为当前系统时间
	return {
		getDate: function() {
			return _date;
		},
		setDate: function(date) {
			_date = date;
		}
	};
})();


export default {
	components: {

	},
	data() {
		return {
			timequantum: [],//时间段集合
			weeks: [],
			currentIndex:5,
			currentDate:1,
			currentMonth:1,//当前月份
			currentYear:2018,//当前年份
			currentMonthText:1 //显示月份
		}
	},
	onLoad: function() {
		
		uni.setNavigationBarTitle({
			title: "默认导航栏"
		})
		//获取当前时间 所属年份 所属月份 所属日期 所属时间段 第几周
		this.currentDate=dateObj.getDate().getDate();
		this.currentMonth=dateObj.getDate().getMonth() + 1;
		this.currentYear = dateObj.getDate().getFullYear();
		this.weeks = this.showCalendarData1(this.currentMonth);
		this.timequantum=this.getTimequantum(0,20);
		console.log(this.weeks)
		for (let i = 0; i < this.weeks.length; i++) {
			//得到每一组的数据
			for (let j = 0; j < this.weeks[i].data.length; j++) {
					let aa=this.weeks[i].data[i];
					if(this.currentMonth==aa.month&&this.currentYear==aa.year&&this.currentDate==aa.day){
						console.log(i)
					}
			}
		}
	},
	methods: {
		bindLogin(){
			this.weeks = this.showCalendarData1(11);
		},
		formatDay(num) {
			switch (num) {
				case 0:
					return '周一'
					break;
				case 1:
					return '周二'
					break;
				case 2:
					return '周三'
					break;
				case 3:
					return '周四'
					break;
				case 4:
					return '周五'
					break;
				case 5:
					return '周六'
					break;
				case 6:
					return '周日'
					break;
				default:
					break;
			}
		},
		async changeTab(e) {
			let index = e.target.current;
			let month=this.currentMonth
			if(this.currentIndex==0&&index==5){
				//上月第五周
				if(month==1){
					this.currentMonth=12;
					this.currentYear--;
				}else{
					this.currentMonth--
				}
			}
			if(this.currentIndex==5&&index==0){
				//下月第一周
				if(month==12){
					this.currentMonth=1;
					this.currentYear++;
				}else{
					this.currentMonth++
				}
			}
			this.weeks = this.showCalendarData1(this.currentMonth);
			this.currentIndex=index;
		},

		groupArray(data, cols) {
			let obj={};
			const list = [];
			let current = [];
			// for (t of data) {
			data.forEach((t,k) => {
				current.push(t);
				if (current.length === cols) {
					obj={
						k,
						belongToMonth:current[0]['month'],
						data:current
					}
					list.push(obj);
					current = [];
				}
			});
			// }    // for (t of data)
			if(current.length>0){
				obj={
					belongToMonth:current[0]['month'],
					data:current
				}
			}
			if (current.length) {
				list.push(obj);
			}
			return list;
		},
		
		
		
		showCalendarData1(month) {
			var _year = this.currentYear;
			var _month = month;
			var _dateStr = util.getDateStr(dateObj.getDate());

			// 设置顶部标题栏中的 年、月信息
			var calendarTitle = '';
			var titleStr = _dateStr.substr(0, 4) + "年" + _dateStr.substr(4, 2) + "月";
			calendarTitle = titleStr;

			// 设置表格中的日期数据
			var _tds = [];
			var _firstDay = new Date(_year, _month - 1, 1); // 当前月第一天
			//6行7列
			for (var i = 0; i < 42; i++) {
				let obj = {};
				var _thisDay = new Date(_year, _month - 1, i + 1 - _firstDay.getDay() + 1);
				var _thisDayStr = util.getDateStr(_thisDay);
				obj['year']=_year;
				obj['day'] = util.addPreForTime(_thisDay.getDate());
				obj['month'] = _thisDay.getMonth()+1;
				obj['data'] = _thisDayStr; //ex：20181125
				if (_thisDayStr == util.getDateStr(new Date())) { // 当前天
					obj['status'] = 'currentDay';
				} else if (_thisDayStr.substr(0, 6) == util.getDateStr(_firstDay).substr(0, 6)) {
					obj['status'] = 'currentMonth'; // 当前月
				} else { // 其他月
					obj['status'] = 'otherMonth';
				}
				_tds.push(obj)
			}
			return this.groupArray(_tds, 7);
			//console.log(this.week);
		},
		/**
		 * 获取时间段集合 00:00....
		 */
		getTimequantum(start,end){
			let arr=[];
			for (let i = start; i < end+1; i++) {
				arr.push(util.addPreForTime(i) + ':00')
			}
			return arr
		}
	}
}



